@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.navBar{
    display: flex;
    padding: 20px;
    justify-content: space-between;
    align-items: center;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  transition: 0.3s;
}

.coin-search:hover{
    transform: scale(1.1) ;
    transition: 0.3s ;
    box-shadow: 0px 10px 20px 2px rgba(0, 0, 0, 0.25);
}

.coin-text {
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

label {
    width: 50px;
    height: 25px;
    position: relative;
    display: block;
    background: #ebebeb;
    border-radius: 200px;
    box-shadow: inset 0px 5px 15px rgba(0,0,0,0.4), inset 0px -5px 15px rgba(255,255,255,0.4);
    cursor: pointer;
  }

  label:after {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    left: 3px;
    top: 2px;
    background: linear-gradient(180deg,#ffcc89,#d8860b);
    border-radius: 180px;
    box-shadow: 0px 5px 10px rgba(0,0,0,0.2);
  }
  .hidden-input {
    width: 0;
    height: 0;
    visibility: hidden;
  }

  .hidden-input:checked + label {
    background: #242424;
  }

  .hidden-input:checked + label:after {
    left: 48px;
    top: 3px;
    transform: translateX(-100%);
    background: linear-gradient(180deg,#777,#3a3a3a);
  }


  